/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.container {
  width: 100%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.align-center {
  text-align: center;
}

.sm-container {
  width: min(95%, 80rem);
  margin: auto;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 1.5rem 0;
  font-weight: 900;
  color: #0833A2;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

.footer {
  padding: 2rem 0;
  background-color: #0833A2;
  margin-bottom: 0;
}
.footer .footer-content {
  text-align: center;
}
.footer .footer-content .copyright {
  color: #FFFFFF;
}

.header {
  display: flex;
  margin: 2rem 5rem 0 5rem;
  justify-content: space-between;
}
.header .logo {
  display: block;
}
.header .logo p {
  color: #0833A2;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
  text-align: right;
}
.header .logo p span {
  color: #FF6C00;
}
.header .actions {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header .actions .login {
  background-color: #0833A2;
  border-radius: 5rem;
  padding: 1.5rem 2.5rem;
  color: #FFFFFF;
  margin: 0 2rem;
}
.header .actions .carrito {
  background-color: #FF6C00;
  border-radius: 5rem;
  height: 5rem;
  width: 8rem;
  color: #FFFFFF;
  background-image: url("../img/cart.png");
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: center;
}

.search-bar {
  display: flex;
  align-items: center;
  margin: 2rem;
  width: 70%;
}
.search-bar .buscador {
  position: relative;
  flex: 1;
  display: inline-block;
}
.search-bar input[type=text] {
  border: none;
  padding: 1.5rem 3rem;
  background-color: #e1e1e1;
  flex: 1;
  border-radius: 5rem;
  width: 100%;
}
.search-bar .search-button {
  margin: 0 1rem;
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background-image: url("../img/searchIcon.png");
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center;
}

.navegacion {
  display: flex;
  background-color: #0833A2;
  margin-bottom: 2rem;
}
.navegacion a {
  color: #FFFFFF;
  padding: 2rem 5rem;
  font-size: 1.8rem;
}
.navegacion a:hover {
  background-color: #042781;
}
.navegacion a.active {
  background-color: #042781;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    max-width: 120rem;
  }
}
.container-info {
  display: flex;
  justify-content: center;
  align-items: center;
}

.anuncio {
  border: 1px solid #e1e1e1;
  margin-bottom: 5rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  text-align: center;
}
.anuncio .contenido-anuncio p {
  margin: 0;
}
.anuncio .contenido-anuncio .precio {
  margin-top: 3rem;
  color: #FF6C00;
  font-weight: 700;
}

.ver-todos {
  margin: 0 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}
.ver-todos img {
  width: 5rem; /* Adjust the width as needed */
  height: 5rem; /* Adjust the height as needed */
  margin-bottom: 1rem;
}
.ver-todos span {
  color: #FF6C00;
  text-align: center;
}

.publicidad {
  padding: 0 3rem;
  margin: 0 1rem 3rem 1rem;
  background-color: #ff984d;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.publicidad .contenido-publicidad {
  flex-direction: column;
  text-align: center;
}
.publicidad .contenido-publicidad p {
  color: #0833A2;
  font-weight: 700;
  font-size: 3.5rem;
}
.publicidad .contenido-publicidad span {
  font-size: 3rem;
}
.publicidad .flecha {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 10rem;
}

.contacto {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 2rem 10rem;
  margin: auto 0;
}
.contacto__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  margin: 0 auto;
}
.contacto__info p {
  font-size: 2rem;
  color: #929292;
}
.contacto__info p span {
  display: block;
  color: #0833A2;
  text-decoration: underline;
  margin: 1rem 0;
}

.form {
  display: flex;
  flex-direction: column;
}
.form__description {
  margin: 0 0 1rem 0;
  color: #929292;
}
.form__field {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.form__label {
  display: block;
  font-size: 2.2rem;
  color: #FF6C00;
  font-weight: 700;
}
.form__input, .form__textarea {
  border: none;
  background-color: #e1e1e1;
  border-radius: 1rem;
  padding: 1.5rem;
}
.form__submit {
  background-color: #0833A2;
  padding: 1.5rem 5rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 2.5rem;
  width: auto;
  align-self: center;
  margin: 1.5rem;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
.form__submit:hover {
  background-color: #062471;
  cursor: pointer;
}
.form__textarea {
  height: 20rem;
}

.action {
  color: #0833A2;
  margin: 0;
  font-size: 1.6rem;
}
.action span {
  color: #FF6C00;
}